<template>
  <BaseActionButton
    is-flat
    no-border
    :icon="darkTheme ? 'eva-moon-outline' : 'eva-sun-outline'"
    @click="toggleDarkTheme"
  />
</template>

<script>
import { mapState, mapMutations } from "vuex";

export default {
  name: "ThemeSwitcher",

  computed: {
    ...mapState(["darkTheme"]),
  },

  methods: {
    ...mapMutations(["toggleDarkTheme"]),
  },
};
</script>

<style lang="scss" scoped></style>
